.blog{
    padding: 3rem 0
}
.contenedor__blog{
    padding-bottom: 3rem;
    @include grid(1, 0);
    @include tablet {
        @include grid(2, 3rem);

    }
}
.imagen__blog{
    position: relative;
          
    &::before{
        position: absolute;
        content: '21-02-2021';
        padding: 1rem 3rem;
        background-color: $acero;
        color: $blanco;
        font-size: 1.6rem;
        font-weight: $bold;
        right: 0;
        bottom: .333rem;
        border: none;
        
    }
}
.texto__blog{

    h4{
        font-weight: $bold;
        font-size: 1.6rem;
        text-transform: uppercase;
    }
    p{
        font-size: 1.4rem;
        margin-bottom: 1rem;
        span{
            color: $coral;
            font-weight: bold;
        }
    }
    
}
.contenido__blog{
    text-align: center;
    &:first-child{
        padding-bottom: 3rem;
    }
    @include tablet{
        &:first-child{
            padding-bottom: 0;
        }
    }
}