.footer{
    background-color: $gris_claro;
    padding: 3rem 0;
    font-weight: $delgada;
    .footer__contenido{
        display: grid;
        grid-template-columns: 1fr;
        
        @include tablet {
            grid-template-columns: 2fr 1fr 1fr 2fr;
            gap: 3rem;
        }
    }
}
.footer__informacion{
    
    .footer__informacion--texto{
        margin: 3rem 0 0 0;
        line-height: 1.5;
    }
    .phone, .email{
        padding-top: 2rem;
        display: flex;
        gap: 2rem;
        img{
            width: 2rem;
        }
    }

}
.footer__categoria{
    
    .acordeon, .acordeon1{
        position: relative;
        &::before{
            content: '+';
            position: absolute;
            right: 1.5rem;
        }
        &.menos{
            &::before{
                content: '-';
                position: absolute;
                right: 1.5rem;
            }  
        }
        @include tablet{
            &::before{
                content: '';
            }
        }
    }
    
    .footer__titulo{
        
        text-transform: uppercase;
        font-size: 1.6rem;
        font-weight: $black;
        margin: 0;
        padding: 2rem 0;
        
        @include tablet{
            // padding: 1.5rem 0;
        }
    }
    ul{
        padding: 0;
    
        li{
            list-style: none;
            padding-bottom: 2rem;
        }   
    }
    .cont_acordeon,
    .cont_acordeon1{
        background-color: $blanco;
        display: none;
        overflow: hidden;
        transition: all .3s;
        
        
        &.show{
            display: block;
        }
    }
    @include tablet{
        .cont_acordeon1, .cont_acordeon{
            display: block;
            background-color: $gris_claro;
        }
    }
}
.formulario{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    input{
        border: 1px solid $gris;
        padding: 1rem;
        color: $coral;
        outline-color: $coral;
        &::placeholder{
            font-weight: $delgada;
        }
    }
    @include desktop{
        flex-direction: row;
        input{
            padding: 0 0 0 1rem;
        }
    }
}
.cards{
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;

    img{
        width: 5rem;
    }
}

.copyright{
    background-color: $gris_claro;
    font-weight: $delgada;
    font-size: 1.6rem;

    .copyright__contenido{
        border-top: 1px solid #e1e1e1;
        padding: 5rem 0;

        display: flex;
        flex-direction: column;
        @include tablet {
            align-items: center;
            justify-content: space-between;
        }
        @include desktop {
            flex-direction: row;
        }
    }
    .contacto{
        display: flex;
        flex-direction: column;

        p{
            text-align: center;
        }
                
        @include tablet{
            flex-direction: row;
            align-items: center;
            gap: 2rem;
        }
    }
    
}
.redes{

    display: flex;
    gap: 1rem;
    padding: 2rem 0;
    margin: 0 auto;
    
    .red{
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background-color: #e1e1e1;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all .3s ease-in-out;
        &:hover{
            
            background-color: $coral;
            img{
                filter: invert(1);
            }
        }
    }
    @include tablet{
        padding: 0;
        margin: 0;
        
    }
    img{
        width: 2rem;
    }
}
.infoAdicional{
    display: flex;
    flex-direction: column;
    align-items: center;
    a{
        padding: 0 0 2rem 0;
        &:last-of-type{
            padding-bottom: 0;
        }
    }
    
    @include tablet{
        flex-direction: row;
        gap: 1rem;
        a{
            padding: 2rem 0 0 0;
            &::after{
                content: '-';
                margin-left: 1rem;
            }
            &:hover{
                color: $coral;
            }
            &:last-of-type::after{
                content: '';
            }

        }
    }
    @include desktop{
        a{
            padding: 0;
        }
    }
}