.header{
    background-color: $blanco;
    position: fixed;
    width: 100%;
    padding: 3rem 0;
    z-index: 9000;
    transition: .3s;
    &.sticky{
        padding: 1rem 0;
    }
    
    
}
.barra{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .logo{
        font-size: 3rem;
        font-weight: $black;
    }
    .icons{
        display: flex;
        gap: 2rem;
        img{
            width: 2rem;
            cursor: pointer;
        }
    }
    .menu{
        @include tablet{
            display: none;
        }
    }
    
}


.navegacion{
    position: fixed;
    top: 0;
    right: -100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0 1rem;
    background-color: $coral;
    color: $blanco;
    height: 100vh;
    width: 50%;
    z-index: 1000;
    transition: all .3s;
    &.activo{
        right: 0;
    }
    a{
        padding: 2rem ;
        color: $blanco;
        border-bottom: 1px solid $gris_claro;
    }
        
    
    @include tablet {
        position: relative;
        align-items: center;
        right: 0;
        gap: 3rem;
        flex-direction: row;
        justify-content: unset;
        height: auto;
        background-color: unset;
        transition: unset;
        a{
            color: $negro;
            font-size: 1.4rem;
            padding: .5rem 0;
            text-transform: uppercase;
            &:hover{
                color: $coral;
                border-bottom: 3px solid $coral;
            }
        }
        
    }    
}
.close{
    position: fixed;
    color: $blanco;
    font-weight: $black;
    font-size: 2.5rem;
    top: 0;
    right: -100%;
    padding: 1.5rem;
    z-index: 20000;
    transition: all .3s;
    &.activo{
        right: 0;
    }
}