.portada{
    background-color: $gris;
}
.portada__contenido{
    padding: 13rem 0 3rem 0;
    text-align: center;
    .portada__imagen{
        justify-self: center;
        img{
            
            display: none;
        }
    }

    @include tablet{
        text-align: left;
        height: 50rem;
           
        @include grid(2, 3rem);
        align-items: center;
        .portada__imagen{
            justify-self: center;
            img{
                display: block;
                width: 40rem;
            }
        }
    }
}
.portada__info{
    p{
        font-size: 2rem;
        font-weight: $regular;
        margin-bottom: 2rem;
    }
    h2{
        margin-bottom: 4rem;
        font-size: 5rem;
        font-weight: $delgada;
        text-transform: uppercase;
    }


}