.fondo__publicidad {
    height: 40rem;
    background-image: url('../img/background.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;

    section{
        background-color: $blanco;
        max-width: 50rem;
        padding: 3rem;
        text-align: center;
        
    }
    h3{
        display: inline-block;
        font-size: 1.6rem;
        font-weight: $regular;
        padding-bottom: .5rem;
        text-transform: uppercase;
        border-bottom: .2rem solid $coral;
    }
    h4{
        font-size: 2rem;
        font-weight: $delgada;
    }
    p{
        font-size: 1.4rem;
        margin-bottom: 2rem;
        line-height: 2;
    }
}