.go-top{
    position: fixed;
    width: 5rem;
    height: 5rem;
    right: -100%;
    bottom: 2rem;
    background-color: $gris;
    z-index: 200000;
    border-radius: .5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .5s ease-in-out;
    cursor: pointer;
   img{
       width: 3rem;
       
   }
   &.showArrow{
        right: 2rem;
   }
   &:hover{
       background-color: darken($gris, 8);
   }

}