.navegacion__productos{
    background-color: $gris_claro;
}
.navegacion__contenido{
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    .navegacion__items{
        display: flex;
        justify-content: space-around;
        gap: 1rem;
        flex-wrap: wrap;
        align-items: center;
        padding: 2rem 0;
        
        
        &>a{
            text-transform: uppercase;
            margin-bottom: 2rem;
        }
    }
    @include telefono{
        .navegacion__items{

            &>a{
                margin-bottom: 0;
            }     
        }
    }
    @include tablet{
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-top: 0;
        
        .navegacion__items{  
            gap: 3rem;
            &>a{
                padding-bottom: 0;
                
                &:hover{
                    color: $coral;
                    border-bottom: 1px solid $coral;
                }
            }
        }
    }
}