.productos{
    padding: 3rem 0;
    @include grid(1, 0);
    @include telefono{
        @include grid(2, 2rem);
    }
    @include tablet{
        @include grid(4, 2rem);
    }
    .producto{
        position: relative;
        overflow: hidden;
        
        &:nth-child(3n + 2){

            &::before{
                position: absolute;
                content: 'NEW';
                color: $blanco;
                display: flex;
                justify-content: center;
                align-items: flex-end;
                padding: 1rem;
                width: 10rem;
                height: 10rem;
                transform: rotate(45deg);
                background-color: $acero;
                top: -5rem;
                right: -5rem;
            }
        }
    }
}
.producto__info{
    p{
        text-transform: uppercase;
        font-size: 1.4rem;
    }
    span{
        color: $coral;
        font-weight: $black;
    }
}