html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
}
p {
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;
    color: $negro;
}

img {
    max-width: 100%;
}

h1, h2, h3, p {
    font-family: $fuente_principal;
    margin: 0;
    
}
.titulo{
    text-transform: uppercase;
    border-left: .2rem solid $coral;
    padding: 0 1rem;
    font-size: 2rem;
}
.boton{
    display: inline-block;
    text-transform: uppercase;
    border-radius: 5px;
    padding: 1rem 5rem;
    background-color: $coral;
    color: $blanco;
    transition: all .3s ease-in-out;
    &:hover{
        background-color: darken($coral, 3);
    }
}